html, body {
  height: 100%;
  overflow: hidden;
}

.App {
  min-height: 100vh;
  display: flex;
}

#particles {
  width: 100%;
}

#particles canvas {
  background: linear-gradient(30deg, #1d232b 45%, #1d2d42 100%);
}

.info {
  position: absolute;
  color: white;
  font-family: 'Yantramanav', sans-serif;
  white-space: nowrap;
  user-select: none;

  @media all                 { font-size: 10vw; } // Small devices (portrait phones)
  @media (min-width: 576px)  { font-size: 9vw;  } // Small devices (landscape phones)
  @media (min-width: 768px)  { font-size: 8vw;  } // Medium devices (tablets)
  @media (min-width: 992px)  { font-size: 7vw;  } // Large devices (desktops)
  @media (min-width: 1200px) { font-size: 5vw;  } // Extra large devices (large desktops)

  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  text-align: center;

  animation: fade 15s infinite;

  .lab + .lab {
    margin-left: 4px;
  }

  a:visited, a:link, a:active {
    text-decoration: none;
    animation: fade 15s infinite;
  }

  a:hover {
    color: white;
    animation: none;
  }
}


@keyframes fade {
  0%   { color: rgba(255, 255, 255, 0.80); }
  10%  { color: rgba(255, 255, 255, 0.60); }
  20%  { color: rgba(255, 255, 255, 0.55); }
  30%  { color: rgba(255, 255, 255, 0.33); }
  40%  { color: rgba(255, 255, 255, 0.78); }
  50%  { color: rgba(255, 255, 255, 0.80); }
  60%  { color: rgba(255, 255, 255, 0.50); }
  70%  { color: rgba(255, 255, 255, 0.40); }
  90%  { color: rgba(255, 255, 255, 0.63); }
  100% { color: rgba(255, 255, 255, 0.80); }
}
